<script>
import api from "@/services/api"
import GlobalHeader from "@/components/GlobalHeader.vue"
import GlobalMessage from "@/components/GlobalMessage.vue"

export default {
  components: {
    GlobalHeader,
    GlobalMessage
  },
  props: {
    parent_id: {
      required: true
    },
    child_id: {
      required: true
    },
    mask_id: {
      required: true
    }
  },
  data() {
    return {
      mask: null,
      group: null,
      seigi_bool: null,
      SEIGI: 3
    };
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username
    }
  },
  methods: {
    getMaskData: function() {
      api({
        method: "GET",
        url: `/masks/${this.mask_id}/`
      }).then(response => {
        this.mask = response.data
      })
      api({
        method: "GET",
        url: `/getusers2/${this.username}/`
      }).then(response =>{
        this.group = response.data[0].groups
        this.seigi_bool = this.group.includes(this.SEIGI)
      })
    },
    deleteMaskData: function() {
      var result = window.confirm('')
      if(result) {
        api({
          method: "DELETE",
          url: `/masks/${this.mask_id}/`
        }).then(() => {
          this.$router.push({
            name: "child",
            params: {
              parent_id: this.parent_id,
              child_id: this.child_id
            }
          })
        })
      }
    }
  },
  created() {
    this.getMaskData()
  }
};
</script>
