<template>
  <!--  -->
  <div id="header">
    <b-navbar type="dark" variant="dark">
      <a class="navbar-brand" href="/"></a>
      <!-- <a class="navbar-brand" href="/mask"></a> -->
      <a class="navbar-brand" href="/material_list" target="_blank"></a>
      <a class="navbar-brand" href="/customer_list" target="_blank"></a>
      <Bell v-if="isLoggedIn" />
      <b-navbar-nav class="ml-auto" v-if="$route.meta.requiresAuth">
        <b-nav-item-dropdown right v-if="isLoggedIn">
          <template v-slot:button-content>{{ username }}</template>
          <b-dropdown-item href="#" v-on:click="clickLogout">
            
          </b-dropdown-item>
        </b-nav-item-dropdown>
        <b-nav-item href="#" v-on:click="clickLogin" v-else>
          
        </b-nav-item>
      </b-navbar-nav>
    </b-navbar>
  </div>
</template>

<script>
import Bell from "@/views/Bell.vue"

export default {
  components: {
    Bell
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username;
    },
    isLoggedIn: function() {
      return this.$store.state.auth.isLoggedIn;
    }
  },
  methods: {
    // 
    clickLogout: function() {
      this.$store.dispatch("auth/logout");
      this.$store.dispatch("message/setInfoMessage", {
        message: ""
      });
      this.$router.replace("/login");
    },
    // 
    clickLogin: function() {
      this.$store.dispatch("message/clearMessages");
      this.$router.replace("/login");
    }
  }
};
</script>
